$color: #382a3a;
body {
    background-color: #f2f2f2;
    font-family: "Work Sans", sans-serif;
    color: black;
}
.btn {
    background-color: #4439bf;
}
.menubar {
    border: solid 2px;
    height: 80px;
    display: flex;
    justify-content: space-between;
}
.submenu {
    width: 50%;
}
.navbar {
    display: flex;
    width: 100vw;
    height: 80px;
    color: #ffffff;
}
.loginpage {
    margin: 130px 100px;
}
#loginform {
    width: 50%;
}
.row {
    h2 {
        text-align: center;
    }
    height: 670px;
    margin-right: 30px;
    margin-left: 30px;
    border: solid 1px;
    border-radius: 10px;
}
.col-3 {
    height: 100%;
}
.kanban-category-title {
    margin: 30px;
}
.kanban-cards-container {
    max-height: 500px;
    overflow-y: auto;
    overflow-x: hidden;
}
.card {
    margin: auto;
    margin-bottom: 5px;
    background-color: #030027;
    color: white;
    border: hidden;
    border-radius: 10px;
}
.card-body {
    text-align: justify;
}
.card-footer {
    cursor: pointer;
    .fa-trash {
        color: red;
        font-size: 15px;
    }
    .fa-edit {
        color: green;
        font-size: 15px;
    }
}
.addTaskButton {
    font-size: 20px;
    width: 100%;
    display: flex;
    justify-content: center;
    .addButton {
        margin-top: 15px;
        padding: 10px;
        width: 70%;
    }
}

.fa-sign-out-alt {
    cursor: pointer;
    color: black;
    font-size: 20px;
}
.page-footer {
    margin-top: 20px;
    font-size: 15px;
    a {
        color: black;
    }
}
